const mapStateToProps = state => {
    return {
        isAuthenticated: state.userId !== null,
        loading: state.loading,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        onTryAutoSignup: () => dispatch(actions.authCheckState()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(App)
)
